.home{
    background-color: #ffffff;
    margin: 1rem;
    border-radius: 1rem;
    padding: 1rem;
    max-width: 80rem;
}
.home .heading > div{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.5rem;
    font-weight: 600;
    color: #4a4a4a;
    padding: 2rem;
    display: flex;
   
}
.home .heading div > span:first-child{
    flex-grow: 1;
}
.home .heading .heading-links{
    font-size: 1rem;
    font-weight: 400;
    display: flex;
    gap: 1rem;
}
.quick-overview-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:2rem;
    width: 100%;
    margin: 0rem 0;
}

.home .body-container{
    display: flex ;
    gap: 3rem;
    flex-wrap: wrap;
    margin-top: 1rem;   
}
.home .body-container .title{
    font-size: 1.3rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    align-self: center;
    margin-top: 1rem;
}

.home .body-container > div{
    display: flex ;
    flex-direction: column;
    box-sizing: border-box; 


}
.home .revenue{
    flex-grow: 1;
    max-width: 35rem;
    min-width: 20rem;
    gap:1rem
    
}
.home .top-station-container{
    flex-grow: 1;
    gap: 1.5rem;
    align-items: center;
}

.home .recent-activity-container{
    align-items: center;
    gap: 1.5rem;
    max-width: 30rem;
    flex-grow: 1;
}
.home .deliveries-container{
    align-items: center;
    gap: 1.5rem;
    flex-grow: 1;
}
.user-percentage-container{
  
}
.home .deliveries-container{


}
@media (max-width:1100px){

    .home .revenue{
        min-width:18rem;
        max-width: 50rem;

    }
    
    .top-station-container {
    }
    .recent-activity-container{
}
.quick-overview-container{
    justify-content: center;
}
}
@media (max-width:480px){

    .home .revenue{
        flex-basis: 100%;

    }
    .home .body-container{
        display: flex ;
        gap: 3rem;
        margin-top: 1rem;   
    }
    
    .top-station-container {
        flex-basis: 50%;
    }
    .recent-activity-container{
        flex-basis: 50%;
        flex-grow: 1;
    }
   
.quick-overview-container{
    justify-content: center;
}
}
/* quick over view component  <<<<<<<<<<<<<<<<< ============= >>>>>>>>>>>>>>>>>>> */

.quick-overview {
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
    min-width: 11.5rem;
    box-shadow: 0 1px 2px 1px #d8d8d8;
    border-radius: 0.7rem;
    padding: 1rem;
}
.home .quick-overview .data{
    font-size: 2rem;
    font-weight: 400;
    font-family: Arial, Helvetica, sans-serif;
}
.home .quick-overview .title{
    display: flex;
}
.home .quick-overview .title span{
    flex-grow: 1;
}
/* top Station component  <<<<<<<<<<<<<<<<< ============= >>>>>>>>>>>>>>>>>>> */
.top-station table{
    width: 15rem;
    border-spacing: 0;
    
}
.top-station td{
  padding: 0.5rem;
}
.top-station td img{
    width: 2rem;
}

/* Recent Activity component  <<<<<<<<<<<<<<<<< ============= >>>>>>>>>>>>>>>>>>> */

.home .recent-activity{
    display: flex ;
    flex-direction: column ;
    gap: 1rem;
   
   
    
}
.recent-activity > div{
    padding-bottom: 0.2rem;
    border-bottom: solid #dadada;
    border-width: 1px;
}
.recent-activity div .description{
    display: flex;
    flex-direction: row;
    gap: 0.8rem;
    

}
.recent-activity .description img{
    width: 1rem;
}
.recent-activity .description span{
    font-family:Arial, Helvetica, sans-serif;
    font-size: 0.7rem;
}
.recent-activity .time{
    margin-left: 1.8rem;
    font-size: 0.8rem;
    font-weight: 700;
}
/* User Percentage component  <<<<<<<<<<<<<<<<< ============= >>>>>>>>>>>>>>>>>>> */
.user-percentage{
    margin-top: 2rem;
}

/* Deliveries component  <<<<<<<<<<<<<<<<< ============= >>>>>>>>>>>>>>>>>>> */
.deliveries{
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    align-items: center;
    
}
.deliveries .data-delivery{
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    
}
.deliveries div{
    display: flex;
    align-items: center;
}
.deliveries img{
    width: 1.4rem;
    padding-right: 1rem;
}
.deliveries .value{
    font-weight: 600;
    font-family: Arial, Helvetica, sans-serif;
}